/*--------------------*/
.rating {
  display: flex;
  align-items: flex-end;
  font-size: rem(40);
  line-height: 0.75;
  transition: opacity 0.3s ease 0s;
}
.rating__body {
  position: relative;
  letter-spacing: em(8, 30);
}
.rating__body::before {
  content: "★★★★★";
  display: block;
}
.rating__active {
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}
.rating__active::before {
  content: "★★★★★";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  color: #ffd300;
}
.rating__items {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.rating__item {
  flex: 0 0 20%;
  height: 100%;
  opacity: 0;
}
.rating__value {
  display: none;
  font-size: 50%;
  line-height: 1;
  padding-left: rem(10);
}

.rating__info {
  color: #ababab;
  font-size: rem(16);
}

.rating.rating-sending {
  opacity: 0.2;
}

.rating.rating-set .rating__active,
.rating.rating-set .rating__item {
  cursor: pointer;
}

